// BURGER MENU STYLING
.js-open-menu {
    display: flex;
    border: none;
    background: none;
            position: absolute;
            right: 7%;
            top: 50%;
            @media (min-width: 768px) {
                right: 25%;
                top: 80%;
                z-index: 2;
            }
    @media (min-width: 1200px) {
        display: none;
    }
}

.js-menu-container {
    display: block;
    position: fixed;
    top: 0px;
    left: 0;
    // padding: 80px 35px 40px 40px;
    width: 100vw;
    height: 100vh;
    z-index: 200;
    background-color: #E17992;
    transform: translateX(100%);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.js-menu-container.is-open {
    transform: translateX(0);
}
.js-menu-close {
    position: absolute;
    top: 20px;
    // left: 285px;
    right: -290px;
    width: 24px;
    height: 24px;
    padding: 0;
    fill: #fff;
    @media (min-width: 425px) {
        right: -395px;
    }
         @media (min-width: 768px) {
             right: -690px;
              
         }
}
